// Pruebas
const appendLevel = {
    1: 'ero',
    2: 'do',
    3: 'ero',
    4: 'to',
    5: 'to',
    6: 'to',
    7: 'mo',
    8: 'vo',
    9: 'no',
};

export default Vue.extend({
    name: 'RomaUnit',
    props: {
        unit: {
            type: Object,
            required: true,
        },
        index: Number,
    },
    data() {
        return {};
    },
    methods: {
        appendToLevel (level) {
            return appendLevel[level];
        },
        delUnit () {
            this.$store.commit('delUnit', this.index);
        }
    },
});
