










































































































































































import Vue from 'vue';

export default Vue.extend({
    name: 'RomaOrganizer',
    props: {
        expanded: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            expand: this.expanded,
            menuStartDate: false,
            menuEndDate: false,
            startDate: new Date().toISOString().substr(0, 10),
            endDate: new Date().toISOString().substr(0, 10),
            selectedPlan: '',
            selectedUnit: '',
            selectedSubject: '',
            selectedCurse: '',
            selectedLevel: '',
            filters: [
                'Test 1',
                'Test 2',
                'Test 3',
                'Test 4',
                'Test 5',
                'Test 6',
            ],
        };
    },
    computed: {
        isExpanded: {
          get() { return this.expanded ? 0 : -1; },
          set(value) { this.expand = value; },
        },
    },
});
