






















































































































































































import Vue from "vue";
import {mapMutations} from "vuex";

function requestDefaultValues() {
    return {
        name: 'Unidad',
        subject: null,
        schoolLevel: null,
        level: null,
        course: null,
        year: new Date().getFullYear(),
        startDate: new Date().toISOString().substr(0, 10),
        endDate: new Date().toISOString().substr(0, 10),
    }
}

export default Vue.extend({
    name: "AddNewUnitModal",
    props: {
        value: Boolean,
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            startDateMenu: false,
            endDateMenu: false,
            courses: ['A', 'B', 'C', 'D', 'E', 'F'],
            data: [
                {
                    id: 1,
                    name: 'Básica',
                    subjects: [
                        {name: 'Lenguaje', levels: [1,2,3,4,5]},
                        {name: 'Matemáticas', levels: [1,2,3,4,5,6,7,8]},
                        {name: 'Ciencias Naturales', levels: [1,2,3]},
                        {name: 'Historia, Geografía y Ciencias Sociales', levels: [1,2]},
                    ]
                },
                {
                    id: 2,
                    name: 'Media',
                    subjects: [
                        {name: 'Lenguaje', levels: [1,2,3]},
                        {name: 'Matemáticas', levels: [1,2,3,4,5,6]},
                        {name: 'Historia Geográfica / Ciencias Sociales', levels: [1,2]},
                        {name: 'Ciencias Naturales', levels: [1,2,3]},
                    ]
                }
            ],
            request: requestDefaultValues(),
        };
    },
    methods: {
        show (value) {
            this.$emit('input', value)
        },
        ...mapMutations({
            addNewUnit (commit) {
                commit('addNewUnit', this.request);
                this.request = requestDefaultValues();
                this.show(false);
            },
        })
    }
});
